:root {
  --font-primary: "Oswald", "Arial", sans-serif;
  --font-secondary: "Lato", "Arial", sans-serif;
  --color-font-basic: #444444;
  --color-font-basic-active: rgba(68, 68, 68, 0.3);
  --color-font-second: #000000;
  --color-font-second-hover: rgba(0, 0, 0, 0.6);
  --color-font-second-active: rgba(0, 0, 0, 0.3);
  --color-font-third: #111111;
  --color-font-third-hover: #3e3e3e;
  --color-font-third-active: #6b6b6b;
  --color-font-card: #222222;
  --color-font-bg: #ffffff;
  --color-font-bg-hover: rgba(255, 255, 255, 0.6);
  --color-font-bg-active: rgba(255, 255, 255, 0.3);
  --color-font-counter: #f2f2f2;
  --color-bg-basic: #ffffff;
  --color-bg-second: #f2f2f2;
  --color-bg-image: #68b738;
  --color-bg-example: #eaeaea;
  --color-btn-basic: #68b738;
  --color-btn-basic-hover: #5eaa2f;
  --color-btn-basic-active: rgba(94, 170, 47, 0.6);
  --color-btn-more: #f2f2f2;
  --color-btn-more-hover: #ebebeb;
  --color-bd-basic: #d9d9d9;
  --color-bd-second: #cdcdcd;
  --color-bd-third: #e6e6e6;
  --color-bd-input: #e7e7e7;
  --color-bd-card: #ebebeb;
  --color-bd-card-active: rgba(235, 235, 235, 0.3);
  --color-bd-link: #68b738;
  --color-bd-bg: #ffffff;
  --color-bd-input-focus: #444444;
  --color-bd-input-error: #ff8282;
  --color-bd-radio-hover: #bababa;
  --color-bd-radio-hover-opacity: rgba(186, 186, 186, 0.3);
  --color-icon-basic: #000000;
  --color-icon-second: #666666;
  --color-icon-hover: #68b738;
  --color-icon-error: #ff8282;
  --color-icon-plus: #d3d3d3;
  --color-radio-marker: #68b738;
  --padding-page: calc(20px + 0.0223214286 * (100vw - 320px));
}
@media (width >= 768px) {
  :root {
    --padding-page: calc(30px + 0.119047619 * (100vw - 768px));
  }
}
@media (width >= 1440px) {
  :root {
    --padding-page: 110px;
  }
}
@media (width >= 1900px) {
  :root {
    --padding-page: calc(50vw - 840px);
  }
}

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.page {
  height: 100%;
  font-family: var(--font-primary);
  font-size: 16px;
  line-height: 18px;
  color: var(--color-font-basic);
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  background-color: var(--color-bg-basic);
}

.page__header {
  margin-bottom: 27px;
}
@media (width >= 768px) {
  .page__header {
    margin-bottom: 61px;
  }
}
@media (width >= 1440px) {
  .page__header {
    margin-bottom: 69px;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
}

.header {
  position: relative;
  display: block;
}
@media (width >= 768px) {
  .header {
    padding-inline: var(--padding-page);
    display: grid;
    grid-template-columns: min-content 1fr;
    align-items: flex-start;
    padding-top: 25px;
  }
}
@media (width >= 1440px) {
  .header {
    padding: 0;
  }
}
@media (width < 768px) {
  .header--js-menu_closed, .header--js-menu_opened {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--color-bd-basic);
  }
  .header--js-menu_closed.header--index, .header--js-menu_opened.header--index {
    border-bottom: none;
  }
}
.header--index {
  margin: 0;
}
@media (width >= 1440px) {
  .header--index {
    grid-template-columns: 50% 50%;
    margin-bottom: 82px;
  }
  .header--index::before {
    content: "";
    z-index: -1;
    display: block;
    grid-area: 1/2/3/3;
    width: 100%;
    height: 100%;
    background-color: #62a53a;
    background-position: left center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .no-webp .header--index::before {
    background-image: url("../img/background-promo-desktop.jpg");
  }
  .webp .header--index::before {
    background-image: url("../img/background-promo-desktop.webp");
  }
}
@media (width >= 1440px) and (min-resolution: 2dppx) {
  .no-webp .header--index::before {
    background-image: url("../img/background-promo-desktop@2x.jpg");
  }
  .webp .header--index::before {
    background-image: url("../img/background-promo-desktop@2x.webp");
  }
}

.header__logo {
  display: inline-flex;
  align-items: center;
  padding: 13px 20px 14px;
}
@media (width >= 768px) {
  .header__logo {
    row-gap: 13px;
    column-gap: 13px;
    justify-content: start;
    width: max-content;
    margin-right: 32px;
    padding: 0;
  }
}
@media (width >= 1440px) {
  .header__logo {
    grid-area: 1/1/2/2;
    row-gap: 14px;
    column-gap: 14px;
    margin: 0;
    margin-top: 55px;
    margin-left: var(--padding-page);
  }
}

.header__wordmark {
  position: absolute;
  right: var(--padding-page);
  height: fit-content;
  padding-block: 23px;
}
@media (width >= 768px) {
  .header__wordmark {
    position: static;
    right: auto;
    padding: 0;
  }
}
@media (width < 768px) {
  .header--js-menu_closed .header__wordmark, .header--js-menu_opened .header__wordmark {
    left: 50%;
    transform: translateX(-50%);
  }
}

.header__toggle {
  display: none;
  width: 44px;
  height: 44px;
  padding: 0;
  background-color: var(--color-bg-basic);
  border: none;
  cursor: pointer;
}
@media (width < 768px) {
  .header--js-menu_closed .header__toggle {
    display: inline-flex;
    margin-right: 11px;
    padding: 10px;
  }
  .header--js-menu_closed .header__toggle::before {
    content: "";
    width: 24px;
    height: 2px;
    margin-top: 1px;
    background-color: var(--color-icon-basic);
    box-shadow: 0 8px 0 0 var(--color-icon-basic), 0 16px 0 0 var(--color-icon-basic);
  }
  .header--js-menu_opened .header__toggle {
    display: inline-grid;
    margin-right: 7px;
  }
  .header--js-menu_opened .header__toggle::before, .header--js-menu_opened .header__toggle::after {
    content: "";
    grid-area: 1/1/2/2;
    width: 25px;
    height: 2px;
    margin: auto;
    background-color: var(--color-icon-basic);
  }
  .header--js-menu_opened .header__toggle::before {
    transform: rotate(45deg);
  }
  .header--js-menu_opened .header__toggle::after {
    transform: rotate(-45deg);
  }
}
@media (hover: hover) {
  .header__toggle:hover::before, .header__toggle:hover::after, .header__toggle:focus-visible::before, .header__toggle:focus-visible::after {
    opacity: 0.6;
  }
}
.header__toggle:active::before, .header__toggle:active::after {
  opacity: 0.3;
}

@media (width >= 768px) {
  .header__nav {
    justify-self: end;
    margin-right: 3px;
  }
}
@media (width >= 1440px) {
  .header__nav {
    grid-area: 1/2/2/3;
    margin-right: 0;
    padding-inline: 33px var(--padding-page);
    padding-top: 55px;
  }
}
@media (width < 768px) {
  .header--js-menu_closed .header__nav {
    display: none;
  }
  .header--js-menu_opened .header__nav {
    width: 100%;
  }
}
@media (768px <= width < 1440px) {
  .header--index .header__nav {
    margin-bottom: 61px;
  }
}

@media (width < 768px) {
  .header--js-menu_opened .header__list {
    position: absolute;
    right: 0;
    left: 0;
    z-index: 1;
  }
}

.header__promo {
  width: 100%;
}
@media (width >= 768px) {
  .header__promo {
    grid-area: 2/1/3/3;
  }
}

.container {
  padding-inline: var(--padding-page);
}

.logo {
  text-decoration: none;
}
@media (hover: hover) {
  .logo:hover .logo__icon, .logo:hover .logo__wordmark, .logo:focus-visible .logo__icon, .logo:focus-visible .logo__wordmark {
    opacity: 0.8;
  }
}
.logo:active .logo__icon, .logo:active .logo__wordmark {
  opacity: 0.6;
}

.logo__icon {
  display: block;
}
@media (width >= 768px) {
  .logo__icon {
    width: 60px;
    height: 50px;
  }
}
@media (width >= 1440px) {
  .logo__icon {
    width: 70px;
    height: 59px;
  }
}

.logo__wordmark {
  --cat-color: var(--color-font-third);
  --energy-color: var(--color-font-third);
  display: block;
}
@media (width >= 768px) {
  .logo__wordmark {
    --energy-color: var(--color-font-basic);
  }
}
@media (width >= 1440px) {
  .logo__wordmark {
    width: 118px;
    height: 21px;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: var(--color-bg-basic);
  border-top: 1px solid var(--color-bd-third);
}
@media (width >= 768px) {
  .nav__list {
    display: flex;
    flex-wrap: wrap;
    row-gap: 8px;
    column-gap: 33px;
    border-top: none;
  }
}
@media (width >= 1440px) {
  .nav__list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 33px;
    background-color: transparent;
  }
}

.nav__item {
  text-align: center;
  border-bottom: 1px solid var(--color-bd-third);
}
@media (width >= 768px) {
  .nav__item {
    border-bottom: none;
  }
}

.nav__link {
  display: block;
  width: fit-content;
  margin: 0 auto;
  padding: 22px 20px 23px;
  font-size: 20px;
  line-height: 20px;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--color-font-second);
}
@media (width >= 768px) {
  .nav__link {
    padding: 15px 0;
    font-size: 18px;
    line-height: 24px;
    text-align: start;
  }
  .nav__link--current::after {
    content: "";
    display: block;
    width: calc(100% - 2px);
    margin: 0 auto;
    box-shadow: 0 6px 0 1px var(--color-bd-link);
  }
  .header--index .nav__link--current::after {
    box-shadow: 0 8px 0 1px var(--color-bd-link);
  }
}
@media (width >= 1440px) {
  .nav__link {
    padding-block: 17px;
    font-size: 20px;
    line-height: 30px;
  }
  .header--index .nav__link {
    color: var(--color-font-bg);
  }
}
@media (width >= 1440px) and (hover: hover) {
  .header--index .nav__link:not(span):hover, .header--index .nav__link:not(span):focus-visible {
    color: var(--color-font-bg-hover);
  }
}
@media (width >= 1440px) {
  .header--index .nav__link:not(span):active {
    color: var(--color-font-bg-active);
  }
}
@media (width >= 1440px) {
  .header--index .nav__link--current::after {
    box-shadow: 0 6px 0 1px var(--color-bd-bg);
  }
}
@media (hover: hover) {
  .nav__link:not(span):hover, .nav__link:not(span):focus-visible {
    color: var(--color-font-second-hover);
  }
}
.nav__link:not(span):active {
  color: var(--color-font-second-active);
}

.plan {
  padding-bottom: 32px;
}
@media (width >= 768px) {
  .plan {
    padding: 0 calc(70px + 0.0595238095 * (100vw - 768px)) 28px;
  }
}
@media (width >= 1440px) {
  .plan {
    padding: 0 var(--padding-page) 47px;
  }
}

.plan__title {
  padding-inline: var(--padding-page);
  margin: 0;
  margin-bottom: 37px;
  color: var(--color-font-second);
}
@media (width >= 768px) {
  .plan__title {
    margin-bottom: 54px;
    padding-inline: 0;
  }
}
@media (width >= 1440px) {
  .plan__title {
    margin-bottom: 55px;
  }
}

.plan__desc {
  padding-inline: var(--padding-page);
  margin: 0;
  margin-bottom: 39px;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--color-font-second);
}
@media (width >= 768px) {
  .plan__desc {
    margin-bottom: 66px;
    padding-inline: 0;
    font-size: 20px;
    line-height: 30px;
  }
}

.heading {
  font-size: 36px;
  line-height: 36px;
  font-weight: normal;
}
@media (width >= 768px) {
  .heading {
    font-size: 60px;
    line-height: 60px;
  }
}
.heading--subhead {
  position: relative;
  padding-inline: var(--padding-page) 20px;
  font-size: 24px;
  line-height: 26px;
  background-color: var(--color-bg-basic);
}
@media (width >= 768px) {
  .heading--subhead {
    padding-inline: 40px;
    font-size: 36px;
    line-height: 36px;
  }
}
.heading--subhead::before {
  content: "";
  position: absolute;
  left: -100vw;
  z-index: -1;
  width: 200vw;
  box-shadow: 0 16px 0 1px var(--color-bd-link);
}
@media (width >= 768px) {
  .heading--subhead::before {
    box-shadow: 0 23px 0 1px var(--color-bd-link);
  }
}

.form {
  display: grid;
}
@media (width >= 768px) {
  .form {
    grid-template-columns: 1fr 1fr;
    column-gap: 60px;
    align-items: start;
  }
}
@media (width >= 1440px) {
  .form {
    column-gap: 80px;
  }
}

.form__field-group {
  grid-column: span 2;
  margin: 0;
  margin-bottom: 23px;
  padding: 0;
  border: none;
}
@media (width >= 768px) {
  .form__field-group--cat {
    margin-bottom: 72px;
  }
}
@media (width >= 1440px) {
  .form__field-group--cat {
    grid-column: span 1;
    margin-bottom: 70px;
  }
}
.form__field-group--diet {
  margin-bottom: 26px;
  padding-block: 22px 18px;
  border-block: 1px solid var(--color-bd-basic);
}
@media (width >= 768px) {
  .form__field-group--diet {
    margin-bottom: 65px;
    padding: 22px 26px 26px;
    border: 1px solid var(--color-bd-input);
  }
}
@media (width >= 1440px) {
  .form__field-group--diet {
    grid-column: span 1;
    margin-bottom: 70px;
    padding: 24px 27px 24px;
  }
}
.form__field-group--contacts {
  margin-bottom: 24px;
}
@media (width >= 768px) {
  .form__field-group--contacts {
    margin-bottom: 65px;
  }
}
@media (width >= 1440px) {
  .form__field-group--contacts {
    margin-bottom: 70px;
  }
}
@media (width >= 768px) {
  .form__field-group--comment {
    margin-bottom: 65px;
  }
}
@media (width >= 1440px) {
  .form__field-group--comment {
    margin-bottom: 70px;
  }
}
.form__field-group--extra {
  margin-bottom: 32px;
}
@media (width >= 768px) {
  .form__field-group--extra {
    margin-bottom: 52px;
  }
}
@media (width >= 1440px) {
  .form__field-group--extra {
    margin-bottom: 60px;
  }
}

.form__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-inline: var(--padding-page);
  display: flex;
  flex-direction: column;
  row-gap: 14px;
}
@media (width >= 768px) {
  .form__list {
    row-gap: 20px;
    padding: 0;
  }
}
.form__field-group--diet .form__list, .form__field-group--extra .form__list {
