  }
}
@media (width >= 1440px) {
  .form__field-group--extra .form__list {
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    row-gap: 40px;
    column-gap: 80px;
  }
}

.form__legend {
  margin-right: var(--padding-page);
  margin-bottom: 34px;
  color: var(--color-font-second);
}
@media (width >= 768px) {
  .form__legend {
    margin-inline: -40px 0;
  }
}
.form__field-group--contacts .form__legend {
  margin-bottom: 35px;
}
@media (width >= 768px) {
  .form__field-group--contacts .form__legend {
    margin-bottom: 76px;
  }
}
.form__field-group--comment .form__legend {
  margin-bottom: 38px;
}
@media (width >= 768px) {
  .form__field-group--comment .form__legend {
    margin-bottom: 87px;
  }
}
@media (width >= 1440px) {
  .form__field-group--comment .form__legend {
    margin-bottom: 78px;
  }
}
@media (width >= 768px) {
  .form__field-group--extra .form__legend {
    margin-bottom: 57px;
